.spells {
    max-width: 80%;
    padding: 36px 64px;
    margin-top: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px 20px;
}

.spell {
    background-color: rgb(65, 64, 64);
    color: aliceblue;
    width: 200px;
    height: 250px;
    border-radius: 12px;
    padding: 18px 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.spell h3,
.spell label {
    display: block;
    margin-bottom: 5px;
    text-align: left;
}

.spell a:visited,
.spell a:link {
    color: rgb(196, 191, 191);
    text-decoration: none;
}

.spell a:hover {
    color: rgb(0, 0, 0);
}

.title_spell {
    display: flex;
    align-items: center;
    justify-content: center;
}

.title_spell button {
    margin-right: 10px;
    background-color: #99969B;
    border: none;
    border-radius: 15px;
    padding: 16px 32px;
}