@media (min-width: 1350px) {
    .infos {
        color: aliceblue;
        position: absolute;
        top: 80px;
        left: 50%;
        font-size: 20px;
        transform: translateX(-50%);
    }
}

@media (max-width: 1349px) {
    .infos {
        color: aliceblue;
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
}

.infos h5 {
    margin-bottom: 12px;
    display: flex;
    align-items: center;
}

.infos h5 button.button_toggle {
    background-image: url('http://localhost:8085/openimg/eye-open.png');
    background-color: #333;
    background-size: cover;
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    margin-left: 12px;
    outline: none;
}

.infos h5 button.button_toggle:hover {
    background-color: #bdbdbd;
}

.password {
    display: flex;
    align-items: center;
    justify-content: center;
}